}
.timeline-wrapper .timeline {
  min-height: 800px;
}
.timeline-wrapper .timeline {
  position: relative;
}
.timeline-wrapper .timeline:before {
  display: block;
  content: "";
  position: absolute;
  left: 50%;
  top: -20%;
  width: 0;
  z-index: -1;
  height: calc(100% + 150px);
  margin: 34px 0;
  border-left: 2px #ebedf0 dashed;
}
.timeline-wrapper .block-wrapper {
  color: rgba(0, 0, 0, 0.85);
  display: flex;
