}
.timeline-wrapper .text-wrapper .block-title {
  margin: 8px 0 12px;
  font-size: 24px;
  position: relative;
  min-height: 18px;
  line-height: 1.2;
  font-weight: 600;
  font-family: Quicksand, sans-serif;
}
.timeline-wrapper .text-wrapper .block-icon {
  position: absolute;
  left: -40px;
  transform: translateX(calc(-50% + 1px));
}
.timeline-wrapper .text-wrapper .block-content {
  width: 100%;
  font-family: "Poppins", sans-serif;
  font-weight: 300;
  font-size: 14px;
